import React from 'react';

export const Categories = ({ value, onClickCategory }) => {
  const categories = [
    '',
    '',
    '',
    '',
    '',
    '',
  ];

  return (
    <div className="categories">
      <ul>
        {categories.map((categoryName, i) => {
          return (
            <li
              key={i}
              onClick={() => onClickCategory(i)}
              className={value === i ? 'active' : ''}
            >
              {categoryName}
            </li>
          );
        })}
      </ul>
    </div>
  );
};
